.button {
  display: inline-block;
  width: 12.625rem;
  text-align: left;
  position: relative;
  z-index: 1;
  background-color: #408F95;
  padding-top: 0.6875rem;
  padding-left: 2.5rem;
  padding-bottom: 0.9375rem;
  border: 0.0625rem solid #408F95;
  transition: background-color 0.3s;
}

.button span {
  font-size: 0.875rem;
  line-height: 1.5714;
  color: #fff;
  font-family: Gotu, sans-serif;
  font-weight: 400;
  transition: color 0.3s;
  position: static;
}

.button::before {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button::after {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  bottom: -0.25rem;
  right: -0.25rem;
  border: 0.0625rem solid #408F95;
  z-index: -2;
}

.button span::after {
  content: "";
  display: inline-block;
  width: 2.5rem;
  height: 0.5rem;
  border-bottom: 0.0625rem solid #fff;
  border-right: 0.0625rem solid #fff;
  position: absolute;
  top: 40%;
  transform: skew(45deg);
  margin-left: 0.3125rem;
  transition: border 0.3s;
}

.button:hover span {
  color: #408F95;
}

.button:hover::before {
  transform: scale(1, 1);
}

.button:hover span::after {
  border-color: #408F95;
}

.button--form {
  padding-left: 3.5rem;
}

.button--reverse {
  background-color: #fff;
  border-color: #fff;
}

.button--reverse::before {
  background: #408F95;
}

.button--reverse span {
  color: #408F95;
}

.button--reverse::after {
  border-color: #fff;
}

.button--reverse span::after {
  border-bottom: 0.0625rem solid #408F95;
  border-right: 0.0625rem solid #408F95;
}

.button--reverse:hover span {
  color: #fff;
}

.button--reverse:hover span::after {
  border-color: #fff;
}

