    margin-top: 1.875rem;
  }
}

.voice__button {
  text-align: center;
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .voice__button {
    margin-top: 5rem;
  }
}

.widget-archive__body {
  margin-top: 1.5rem;
}

.widget-archive__accordion:not(:first-child) {
  margin-top: 0.1875rem;
}

.widget-campaign__panels {
  margin-top: 1.5rem;
}

.widget-campaign__panel:not(:first-child) {
  margin-top: 2.5rem;
}

.widget-campaign__button {
  margin-top: 2.5rem;
  text-align: center;
}

.widget-title {
  position: relative;
  display: flex;
  align-items: center;
  padding-top: 0.4375rem;
  padding-bottom: 0.4375rem;
  padding-left: 4.625rem;
  border-left: 0.5rem solid #408F95;
}

.widget-title::before {
  content: "";
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  width: 3.125rem;
  height: 3.125rem;
  background-image: url(../images/common/widget-title-logo.webp);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 10;
}

.widget-title > span {
  font-size: 1.5rem;
  font-weight: 400;
  color: #0D2936;
}

.widget-voice__items {
  margin-top: 1.4375rem;
}

.widget-voice__item > a {
  transition: opacity 0.3s;
}

.widget-voice__item > a:hover {
  opacity: 0.6;
}

.widget-voice__image img {
  aspect-ratio: 294/218;
  object-fit: cover;
}

.widget-voice__box {
  margin-top: 1rem;
}

.widget-voice__meta {
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 1.5;
  color: #0D2936;
}

.widget-voice__item-title {
  font-size: 1.5rem;
  font-weight: 400;
  color: #0D2936;
  margin-top: 0.5rem;
}

.widget-voice__button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2.625rem;
}

.wp-pagenavi {
  display: flex;
  justify-content: center;
  gap: 0.75rem;
}

.wp-pagenavi a, .wp-pagenavi span {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2.375rem;
  height: 2.375rem;
  border: 0.0625rem solid #408F95;
  color: #408F95;
  transition: color 0.3s, background-color 0.3s;
}

.wp-pagenavi a:hover {
  color: #fff;
  background-color: #408F95;
}

.wp-pagenavi span.current {
  color: #fff;
  background-color: #408F95;
}

.wp-pagenavi a.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .wp-pagenavi a.u-desktop {
    display: inline-flex;
  }
}

.previouspostslink {
  margin-right: 1.75rem;
}

.nextpostslink {
  margin-left: 1.75rem;
}

.wp-pagenavi.wp-pagenavi--arrow {
  gap: 5.125rem;
}

.wp-pagenavi--arrow .previouspostslink,
.wp-pagenavi--arrow .nextpostslink {
  position: relative;
  margin: 0;
}

.wp-pagenavi--arrow .previouspostslink::before,
.wp-pagenavi--arrow .nextpostslink::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0.70625rem;
  height: 0.70625rem;
  border-bottom: 0.0625rem solid #408F95;
}

.wp-pagenavi--arrow .previouspostslink::before {
  border-left: 0.0625rem solid #408F95;
  transform: translate(-25%, -50%) rotate(45deg);
}

.wp-pagenavi--arrow .nextpostslink::before {
  border-right: 0.0625rem solid #408F95;
  transform: translate(-75%, -50%) rotate(-45deg);
}

.wp-pagenavi--arrow .previouspostslink:hover::before,
.wp-pagenavi--arrow .nextpostslink:hover::before {
  border-color: #fff;
}